<template>
    <main class="main">
        <div class="chart_box">
            <div v-if='selectName === 1'>
                <h1 @click='getName(false)'>[匿名聊天]</h1>
                <h1 @click='getName(true)'>[暱稱聊天]</h1>
            </div>
            <div v-if='selectName === 2' class="select_name">
                <p>暱稱聊天</p>
                <input type="text" v-model='setName' placeholder="輸入暱稱">
                <h1 @click='rename()'>[確定]</h1>
            </div>
            <div v-if='selectName === 3'>
                <h1 @click="ramdomChart">[隨機配對1對1聊天]</h1>
                <h1 @click='allChart()'>[進入聊天大廳]</h1>
            </div>
        </div>
    </main>
</template>

<script>
export default {
    name: 'start',
    data(){
        return {
            selectName: 1,
            setName: null,
        }
    },
    computed: {
        name(){
            return this.setName || '匿名'
        }
    },
    methods: {
        getName(boolean){
            boolean ? this.selectName = 2 : this.selectName = 3;
        },
        rename() {
            if(this.name){
                this.selectName = 3;
            }
        },
        allChart(){
            this.$store.commit('setName', this.name);
            this.$router.push({name: 'chart'});
            this.$socket.emit('enter', this.name);
        },
        ramdomChart(){
            alert('功能尚未完成');
        }
    }
}
</script>

<style lang="scss" scoped>
    $bg_color: #1E1E1E;
    $box_color: #252526;
    $border_color: #6C6C6C;
    $text_color: #D4D4D4;
    $hover_color: #D7BA7D;
    $name_color: #9CDCFE;
    .main {
        width: 100vw;
        height: 100vh;
        background-color: $bg_color;
        text-align: center;
        &::after {
            content: '';
            display: inline-block;
            width: 1px;
            height: 100%;
            vertical-align: middle;
        }
    }
    .chart_box {
        display: inline-block;
        width: 250px;
        height: 250px;
        background-color: $box_color;
        vertical-align: middle;
        box-shadow: 0 3px 30px rgba(0, 0, 0, 0.48);
        h1 {
            color: $text_color;
            font-size: 1.25rem;
            margin: 40px 0;
            cursor: pointer;
            &:hover {
                color: $hover_color;
            }
        }
        input {
            border: none;
            outline: none;
            background-color: transparent;
            color: $border_color;
            border-bottom: 1px solid $border_color;
            text-align: center;
        }
        >div {
            display: inline-block;
            vertical-align: middle;
        }
        &::after {
            content: '';
            display: inline-block;
            width: 1px;
            height: 100%;
            vertical-align: middle;
        }
    }
    .select_name {
        h1 {
            margin: 20px 0;
        }
        p {
            color: $name_color;
            font-size: 1.25rem;
            font-weight: bold;
            margin: 20px 0;
        }
    }
</style>
